import * as actionTypes from './shopping-types';

const INITIAL_STATE = {
    product: [
        {
            id: 1,
            title: "Oranges",
            description: "Oranges from Spain",
            price: 15.0,
            image: "https://cdn-prod.medicalnewstoday.com/content/images/articles/272/272782/oranges-in-a-box.jpg"
        },
        {
            id: 2,
            title: "Apples",
            description: "Apples from Germany",
            price: 25.0,
            image: "data:image/jpeg;base64,"
        },
        {
            id: 3,
            title: "Banana",
            description: "Banana from Africa",
            price: 10.0,
            image: "data:image/jpeg;base64,"
        }
    ],
    cart: [],
    currentItem: null
}


const shopReducer = (state = INITIAL_STATE , action) => {
    switch(action.type) {
        case actionTypes.ADD_TO_CART:
                // Get the items data from the products array
                const item = state.product.find((prod) => prod.id === action.payload.id);
                // Check if the Item is in cart already
                const inCart = state.cart.find((item) =>
                    item.id === action.payload.id ? true : false
                );
                return {
                    ...state,
                    cart: inCart
                      ? state.cart.map((item) =>
                          item.id === action.payload.id
                            ? { ...item, qty: item.qty + 1 }
                            : item
                        )
                      : [...state.cart, { ...item, qty: 1 }],
                }
        case actionTypes.REMOVE_FROM_CART:
            return {
                ...state,
                cart: state.cart.filter((item) => item.id !== action.payload.id),
            };
        case actionTypes.CLEAR_CART:
            return {
                ...state,
                cart: []
            };
        case actionTypes.ADJUST_QTY:
            return {
                ...state,
                cart: state.cart.map( (item) => item.id === action.payload.id ? {...item, qty: action.payload.qty}: item)
            }
        case actionTypes.LOAD_CURRENT_ITEM:
            return {
                ...state,
                currentItem: action.payload,
            }
        default:
            return state;
    }
}   

export default shopReducer;